import * as React from "react"
import { Link } from "gatsby"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  if (isRootPath) {
    header = (
    <>
      <h1 className="main-heading">
        <Link to="/">{title}</Link>
      </h1>
        <p>
          
    <a href="https://kyukyunyorituryo.github.io/"></a>
    <a href="https://kyukyunyorituryo.github.io/kindle_sale/">kindle</a>
    <a href="https://kyukyunyorituryo.github.io/new_pub/"></a>
    <a href="https://kyukyunyorituryo.github.io/i/"> </a>  </p>
<p>amazonKindle</p>
<p><Link to="/kadokawa">KADOKAWA</Link><Link to="/kodansha"></Link><Link to="/akitashoten"></Link><Link to="/hakusensha"></Link><Link to="/shueisha"></Link><Link to="/tokuma"></Link><Link to="/shougakukan"></Link></p>
<p><Link to="/manga"></Link><Link to="/lightnovel"></Link><Link to="/novel"></Link></p>
<p><Link to="/lastmonth"></Link><Link to="/thismonth"></Link><Link to="/nextmonth"></Link><Link to="/list"></Link></p>

      </>
    )
  } else {
    header = (
        <>
      <Link className="header-link-home" to="/">
        {title}
      </Link>
      <p>
    <a href="https://kyukyunyorituryo.github.io/"></a>
    <a href="https://kyukyunyorituryo.github.io/kindle_sale/">kindle</a>
    <a href="https://kyukyunyorituryo.github.io/new_pub/"></a>
    <a href="https://kyukyunyorituryo.github.io/i/"> </a>  </p>
<p>amazonKindle</p>

<p><Link to="/kadokawa">KADOKAWA</Link><Link to="/kodansha"></Link><Link to="/akitashoten"></Link><Link to="/hakusensha"></Link><Link to="/shueisha"></Link><Link to="/tokuma"></Link><Link to="/shougakukan"></Link></p><p><Link to="/manga"></Link><Link to="/lightnovel"></Link><Link to="/novel"></Link></p>
<p><Link to="/lastmonth"></Link><Link to="/thismonth"></Link><Link to="/nextmonth"></Link><Link to="/list"></Link></p>

      </>
    )
  }

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <main>{children}</main>
      <footer>
         {new Date().getFullYear()},
        {` `}
        <a href="https://github.com/kyukyunyorituryo">kyukyunyorituryo</a>
      </footer>
    </div>
  )
}

export default Layout
